import React from 'react'
import styled from 'styled-components'
import Img1 from './../images/flame-water-drops.svg'
import Layout from './../layout/index'
import { SEO, StripeBar, Button } from './../components'
import { useLocation } from '@reach/router'

const Content = styled.div`
  color: #387896;
`
export default function index() {
  const loc = useLocation()
  const isFr = loc.pathname.startsWith('/fr/')
  return (
    <Layout location={{ pathname: '/404' }}>
      <StripeBar />
      <SEO
        pathname={'/404'}
        keyword={'not found'}
        description={'not found'}
        title='Home'
      />
      <div className='container py-5'>
        <div className='d-flex flex-wrap'>
          <div className='col-md-4'>
            <img src={Img1} alt='flame water' />
          </div>
          <Content className='col-md-8 pl-0 pl-md-5 d-flex flex-column'>
            <div className='my-auto'>
              <h1>{isFr ? 'Page non trouvée' : 'Page Not Found'}</h1>
              <h2>
                {isFr
                  ? 'Nous ne trouvons pas la page que vous recherchez.'
                  : "We can't find the page you're looking for."}
              </h2>
              <p className='mb-5'>
                {isFr
                  ? 'Vous pouvez revenir à la page précédente ou visiter notre page d’accueil.'
                  : 'You can either return to the revious page or visit our homepage.'}
              </p>
              <Button urlType='internal' type='url' background='white' to='/'>
                {isFr ? 'Retour à la page d’accueil' : 'Return to Homepage'}
              </Button>
            </div>
          </Content>
        </div>
      </div>
    </Layout>
  )
}
